function findCoinBySymbol (_symbol) {
  for (var i = 0; i < coins.length; i++) {
    if (coins[i].symbol === _symbol) {
      return {
        symbol: coins[i].symbol,
        host: coins[i].host,
        abi: coins[i].abi,
        address: coins[i].address
      }
    }
  }
  return ''
}

export default { findCoinBySymbol, coins, mainSSAbi, mainSSAddress }



