<template>
  <div class="background">
      <div  style=" width:100%; ">
        <div class="floatCenter" style="width: 60rem">
      <!--part1-->
        <div style="height: 23rem; margin-top:5rem; text-align: left;">
          <!--<div style="font-size: xx-large; margin-top: 1.2rem;color: white">2018.12.06 coming soon</div>-->
          <div style="font-size: xx-large; margin-top: 1.2rem">BLOCK OF LIBERTY</div>
          <div style="font-size: x-large; margin-top: 1.2rem">富客 . 全球首个区块链去中心化交平台</div>
          <div style="font-size: x-large; margin-top: 1.2rem">发行数字货币助力名人大咖回馈粉丝 实体企业资产上链  融资  股权激励 IPO上市预演 </div>
          <div style="font-size: x-large; margin-top: 1.2rem; ">以太坊智能合约交易  规则透明  可信任  可追溯  24H自由交易</div>
          <div style="font-size: x-large; margin-top: 1.2rem; ">开业期一折上币  免交易费  免年费  送源码</div>
        </div>
      <!--part2-->
          <div style="margin-right: 1.5rem; text-align: right">
            <button @click="goTradingCenter" style="color: white; font-size: large; background: transparent; cursor: pointer; border:none;">交易中心</button>
          </div>
      <div class="floatCenter" style="height: 5rem; margin-bottom: 2rem">
          <token-head-v-m class="tokenHead" style="width: 18rem" :tokenSymbol="token1Symbol" :token-price="token1Price" ></token-head-v-m>
          <token-head-v-m class="tokenHead" style="width: 18rem" :tokenSymbol="token2Symbol" :token-price="token2Price" ></token-head-v-m>
          <token-head-v-m class="tokenHead" style="width: 18rem" :tokenSymbol="token3Symbol" :token-price="token3Price" ></token-head-v-m>
      </div>
      </div>
    </div>

    <!--pata3-->
    <div style="width:100%; height: 29rem; background: linear-gradient(to right, #B292FF, #8FBDFF)">
        <div class="floatCenter">
            <div style="text-align: left;width: 60rem; margin-top: 3rem">
            <div style="font-family: serif; font-size: xx-large">感恩回馈</div>
            <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">发行数字货币助力名人回馈粉丝 实体企业资产上链 股权激励 IPO预演 员工回馈</div>
            <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">交易返利 推荐返利 盈利分红 全方位回馈持币者 总量500万以下免审核</div>
            <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">持币做矿主 智能合约程式化分红和股息机制将利润所得分于持币者 持币即挖矿</div>
            <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">无需编程 专人辅助 新张期一折酬宾 送交易网站 送源码</div>
            <button @click="goApply" class="buttonComm" style="font-size: large; margin-top: 3rem">申请发行数字货币</button>
            </div>
        </div>
    </div>

    <!--pata4-->
    <div style="width:100%; height: 29rem; background: linear-gradient(to right, #8FBDFF, #B292FF)">
      <div class="floatCenter">
        <div style="text-align: right; width: 60rem; margin-top: 3rem">
          <div style="font-family: serif; font-size: xx-large">去中心化交易所</div>
          <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">架构于以太坊公链全球分布式网络</div>
          <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">无服务器 无中心 永不停歇</div>
          <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">跟智能合约交易 源码公开 可独立单币交易 不依赖交易所 可信任 可追溯</div>
          <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">用户直接链上交易 匿名 没有中间层 交易平台不托管用户资产 安全可靠</div>
          <button @click="goWriteBook" class="buttonComm" style="font-size: large; margin-top: 3rem">白皮书</button>
        </div>
      </div>
    </div>

    <!--pata5-->
    <div style="width:100%; height: 29rem; background: linear-gradient(to right, #B194FF, #50D075)">
      <div class="floatCenter">
        <div style="width: 100%; margin-top: 3rem">
          <div style="font-family: serif; font-size: xx-large">N0 ICO</div>
          <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">拒绝ICO 所有币种的总量 价格均从0开始</div>
          <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">价格由智能合约产生 代码即法律 无法人为操纵 没有爆涨爆跌</div>
          <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">任意时刻均可卖出 无需对手盘</div>
          <div style="font-family: Helvetica; font-size: large; margin-top: 2rem">智能合约架设于以太坊公链 不可更改 规则简单透明 无法暗箱操作 </div>
          <button @click="goSourceCode" class="buttonComm" style="font-size: large; margin-top: 3rem">源 码</button>
        </div>
      </div>
    </div>

    <!--boot-->
    <div class="excessive" style="padding-top: 2rem; padding-bottom: 5rem; border-top: 1px solid white;">
      <myFoot/>
    </div>
  </div>
</template>

<script>
import myTitle from '../components/Title'
import myFoot from './FootEx'
import tokenHeadVM from './tokenHead'
import tokenHeadjs from '../js/tokenHead'
import router from '../router'

export default {
  name: 'home',
  components: {myTitle, myFoot, tokenHeadVM},
  data: function () {
    return {
      tokens: ['SS', 'EVX', 'COVA', 'tt'] // 本页面需要加载的token
    }
  },
  computed: { // 计算属性可用于检测store值的变化
    token1Symbol: function () {
      let tokenHeadList = this.$store.state.tokens.tokensHeadList
      if (tokenHeadList.length > 0) {
        for (let i = 0; i < tokenHeadList.length; i++) {
          if (tokenHeadList[i].symbol === this.tokens[this.tokens.indexOf('SS')]) {
            return tokenHeadList[i].symbol
          }
        }
      }
      return ''
    },
    token1Price: function () {
      let tokenHeadList = this.$store.state.tokens.tokensHeadList
      if (tokenHeadList.length > 0) {
        for (let i = 0; i < tokenHeadList.length; i++) {
          if (tokenHeadList[i].symbol === this.tokens[this.tokens.indexOf('SS')]) {
            return tokenHeadList[i].price
          }
        }
      }
      return ''
    },
    token2Symbol: function () {
      let tokenHeadList = this.$store.state.tokens.tokensHeadList
      if (tokenHeadList.length > 0) {
        for (let i = 0; i < tokenHeadList.length; i++) {
          if (tokenHeadList[i].symbol === this.tokens[this.tokens.indexOf('EVX')]) {
            return tokenHeadList[i].symbol
          }
        }
      }
      return ''
    },
    token2Price: function () {
      let tokenHeadList = this.$store.state.tokens.tokensHeadList
      if (tokenHeadList.length > 0) {
        for (let i = 0; i < tokenHeadList.length; i++) {
          if (tokenHeadList[i].symbol === this.tokens[this.tokens.indexOf('EVX')]) {
            return tokenHeadList[i].price
          }
        }
      }
      return ''
    },
    token3Symbol: function () {
      let tokenHeadList = this.$store.state.tokens.tokensHeadList
      if (tokenHeadList.length > 0) {
        for (let i = 0; i < tokenHeadList.length; i++) {
          if (tokenHeadList[i].symbol === this.tokens[this.tokens.indexOf('COVA')]) {
            return tokenHeadList[i].symbol
          }
        }
      }
      return ''
    },
    token3Price: function () {
      let tokenHeadList = this.$store.state.tokens.tokensHeadList
      if (tokenHeadList.length > 0) {
        for (let i = 0; i < tokenHeadList.length; i++) {
          if (tokenHeadList[i].symbol === this.tokens[this.tokens.indexOf('COVA')]) {
            return tokenHeadList[i].price
          }
        }
      }
      return ''
    }
  },
  watch: {
    buyInput: function () {
      this.buyInput = (this.buyInput.match(/^\d*(\.?\d{0,4})/g)[0]) || null
    },
    sellInput: function () {
      if (this.sellInput.substring(0, 1) !== '0') { // 不能以0开关
        this.sellInput = this.sellInput.replace(/[^\d]/g, '') // 只接整形数字
      } else {
        this.sellInput = ''
      }
    }
  },
  beforeCreate () {
    document.querySelector('body').setAttribute('style', 'margin: 0; padding:0;')
  },
  // 钩子函数 组件加载时执行
  mounted () {
    console.log(this.$options.name, 'log in')
    if (tokenHeadjs.checkExpress() === 1) {
      tokenHeadjs.initContract(this.tokens)
    }
  },

  // 方法集合
  methods: {
    goTradingCenter () {
      router.push({name: 'token', query: {token: this.$store.getters.getSysTokenSymbol}})
    },
    goApply () {
      router.push({name: 'apply'})
    },
    goSourceCode () {
      // router.push({name: 'SourceCode'})
      // window.location.href = 'https://github.com/freeblockworld/freeblock.wrold/blob/master/seashells'
      window.open('https://etherscan.io/address/0x5cf4C6eE0d1F6F9337AB89A79F1885C748b8beE1#code')
    },
    goWriteBook () {
      // router.push({name: 'WriteBook'})
      window.open('https://github.com/freeblockLuxe/Freeblock.luxe/blob/master/EXChange%20whitebook.pdf')
    }
  },

  // 钩子函数 组件结束前执行
  beforeDestroy () {
    console.log(this.$options.name, 'log out')
    document.querySelector('body').removeAttribute('style')
  }
}
</script>

<style scoped>

  .background {
    text-align: center;
    color: white;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, #9980D6, #4BBF71);
  }
  .floatCenter {
    margin: 0 auto;
    display: inline-block;
  }
  .buttonComm {
    color: white;
    width: 12rem;
    height: 3rem;
    font-family: Helvetica;
    background: transparent;
    border:1px solid white;
    border-radius: 0.3rem;
    box-shadow: 0 0 1rem white;
    cursor: pointer;
  }
  .tokenHead {
    float: left;
    width: 18rem;
    border:1px solid white;
    border-radius: 0.3rem;
    box-shadow: 0 0 1rem white;
  }
  .excessive {
    text-align: center;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, #9980D6, #4BBF71);
  }

</style>



